import React from 'react'

class AnimatedNumber extends React.Component {

    state = {
        numberInitial: 0,
        numberFinal: 0,
        intervalId: 0,
        started: false
    }

    componentDidMount() {
        const intervalId = setInterval(this.increment, 200)
        this.setState({ ...this.state, numberFinal: this.props.number, intervalId: intervalId, started: true })
    }

    increment = () => {
        if (this.state.started) {
            let { numberInitial, numberFinal } = this.state

            numberInitial += 5

            if (numberInitial >= numberFinal) {
                clearInterval(this.state.intervalId)
                this.setState({ ...this.state, started: false, numberInitial: numberFinal })
            } else {
                this.setState({ ...this.state, started: false, numberInitial: numberInitial })
            }
        }
    }

    render() {
        return this.state.numberInitial
    }
}

export default AnimatedNumber